<template>
  <div>
    <NavTemplent title=""></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold"></h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            @change="getMaterialList"
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
<!--          <el-form-item>-->
<!--            <el-button type="primary">-->
<!--              <i class="el-icon-search"></i>-->
<!--              </el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <el-row>
        <el-col :span="24">
          <div id="myChart" style="width: 100%;height: 400px"></div>
        </el-col>
      </el-row>
      <div class="tableTitle">
        <span style="width: 100%;">
          <div></div>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="downLoad"><i class="el-icon-download"></i></el-button>
          <el-button size="mini" type="primary" @click="editMaterialsMessage"><i class="el-icon-plus"></i></el-button>
        </span>
      </div>
      <el-table
        :data="materialList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.package_price || '--'}} {{scope.row.package_num}})</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.zipper_package_price || '--'}} {{scope.row.zipper_package_num}})</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.ticket_num || '--'}} {{scope.row.ticket_price}})</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.total_price || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 0"></span>
            <span v-else-if="scope.row.status === 1"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-link @click="editMaterialsMessage(scope.row)" style="color:#409EFF"></el-link>
            <el-link @click="delMaterialMessage(scope.row)" style="color:#409EFF"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageNum" :pageNum="page.pageSize" :total="total" @update:pageSize="updateSize" @update:pageNum="updateNum"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="">
            <el-input style="width: 40%" v-model="form.package_num" placeholder="50"></el-input>
            <el-input style="width: 40%" v-model="form.package_price" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input style="width: 40%" v-model="form.zipper_package_num" placeholder="50"></el-input>
            <el-input style="width: 40%" v-model="form.zipper_package_price" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input style="width: 40%" v-model="form.ticket_num" placeholder="50"></el-input>
            <el-input style="width: 40%" v-model="form.ticket_price" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.web_id" placeholder="">
              <el-option v-for="item in siteList" :key="item.web_id" :value="item.web_id" :label="item.web_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="form.buy_time"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <span>{{form.total_price || 0}}</span>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="form.img_path" :src="form.img_path" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle"> </el-button>
          <el-button size="mini" type="primary" @click="save"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                siteList: [],
                imageUrl: '',
                title: '',
                date: '',
                editMaterialMessage: null,
                materialList: [],
                buyTimeList: [],
                totalNums: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                dialogVisible: false,
                total: 0,
                isDownload: '0',
                form: {
                    web_id: '',
                    buy_time: '',
                    package_num: '',
                    package_price: '',
                    zipper_package_num: '',
                    zipper_package_price: '',
                    ticket_num: '',
                    ticket_price: '',
                    total_price: '',
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    img_path: ''
                }
            }
        },
        methods: {
            // 
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: 10000,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            handleAvatarSuccess(res, file) {
                this.form.img_path = URL.createObjectURL(file.raw);
            },
            drawLine() {
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 
                myChart.setOption({
                    title: {text: ''},
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: this.buyTimeList
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 932, 1330, 901, 1290, 934, 1320],
                        type: 'line',
                        areaStyle: {}
                    }]
                });
            },
            getMaterialList() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('/getMaterialList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    lastTime: this.lastTime,
                    startTime: this.startTime,
                    download: this.isDownload,
                }).then(res => {
                    this.total = res.count;
                    this.materialList = res.list;
                    this.buyTimeList = res.list.map(item => {
                        return moment(item.buy_time * 1000).format('YYYY/MM/DD')
                    });
                    this.totalNums = res.list.map(item => {
                        return item.total_price
                    });
                    if (this.buyTimeList.length && this.totalNums.length) {
                        this.drawLine()
                    }
                })
            },
            updateSize(val) {
                this.page.pageSize = 1;
                this.page.pageNum = val;
                this.getMaterialList();
            },
            updateNum(val) {
                this.page.pageSize = val;
                this.getMaterialList();
            },
            downLoad() {
                this.isDownload = 1;
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$exportExcel('/getMaterialList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    lastTime: this.lastTime,
                    startTime: this.startTime,
                    download: this.isDownload,
                }).then(() => {
                    this.$message.success('')
                })
            },
            editMaterialsMessage(data) {
                this.editMaterialMessage = data;
                this.dialogVisible = true;
                if (data.material_cost_id) {
                    this.form = data;
                    this.form.buy_time = data.buy_time * 1000
                }else {
                    this.form.package_num = '';
                    this.form.package_price = '';
                    this.form.zipper_package_num = '';
                    this.form.zipper_package_price = '';
                    this.form.ticket_price = '';
                    this.form.ticket_num = '';
                    this.form.buy_time = '';
                    this.form.img_path = ''
                }
            },
            delMaterialMessage(data) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$post('delMaterial', {
                        material_cost_id: data.material_cost_id
                    }).then(() => {
                        this.$message.success('');
                        this.getMaterialList();
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editMaterialMessage.material_cost_id) {
                    this.$post('editMaterial', {
                        material_cost_id: this.editMaterialMessage.material_cost_id,
                        package_num: this.form.package_num,
                        package_price: this.form.package_price,
                        zipper_package_num: this.form.zipper_package_num,
                        zipper_package_price: this.form.zipper_package_price,
                        ticket_price: this.form.ticket_price,
                        ticket_num: this.form.ticket_num,
                        buy_time: moment(this.form.buy_time).format('YYYY/MM/DD'),
                        image: this.form.img_path
                    }).then(() => {
                        this.$message.success('');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getMaterialList()
                    })
                } else {
                    this.$post('addMaterial', this.form).then(res => {
                        this.$message.success('');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getMaterialList()
                    })
                }
            }
        },
        mounted() {
            this.getSiteList();
            this.getMaterialList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select, .date{
    width: 100%;
  }
  /deep/ .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



